<template>
  <layout-default>
    <div class="page-content">
      <div class="container-fluid">

          <!-- start page title -->
          <div class="row">
              <div class="col-12">
                  <div class="page-title-box d-flex align-items-center justify-content-between">
                      <h4 class="mb-0">جدول الموردين</h4>
                      <div class="page-title-right">
                          <button type="button" @click="$refs.FormModal.create()" class="btn btn-success waves-effect waves-light">
                              + إضافة جديد
                          </button>
                          <form-modal ref="FormModal" :fetchData="fetchData"/>
                      </div>

                  </div>
              </div>
          </div>
          <!-- end page title -->
          
          <div class="row">
              <div class="col-12">
                  <div class="card">
                      <div v-if="suppliers&&suppliers.data.length" class="card-body">
                          <table id="datatable" class="table table-bordered dt-responsive nowrap" style="border-collapse: collapse; border-spacing: 0; width: 100%;">
                              <thead>
                              <tr>
                                  <th>الأسم</th>
                                  <th>التليفون</th>
                                  <th>تاريخ التسجيل</th>
                                  <th>نوع المحاسبة</th>
                                  <th>مستحقات</th>
                                  <th>خيارات</th>
                              </tr>
                              </thead>
                              <tbody>
                              <tr v-for="(supplier,index) in suppliers.data">
                                  <td>{{supplier.name}}</td>
                                  <td>{{supplier.phone1}}</td>
                                  <td>{{supplier.created_at| moment("DD/MM/YYYY")}}</td>
                                  
                                  <td v-if="supplier.status==1" style="font-weight: 600;"><p class="float-start text-success">فوري</p></td>
                                  <td v-else style="font-weight: 600;"><p class="float-start text-primary">بعد البيع</p></td>
                                  
                                  <td v-if="supplier.total_amount>0" style="font-weight: 600;"><p class="float-start text-danger">{{moneyFormat(supplier.total_amount,0)}} L.E</p>	&nbsp; متبقي له</td>
                                  <td v-else style="font-weight: 600;"><p class="float-start text-primary">{{moneyFormat(supplier.total_amount*-1,0)}} L.E</p>	&nbsp; متبقي عليه</td>
                                  
                                  <td>
                                    <div class="btn-group">
                                        <button type="button" class="btn btn-light dropdown-toggle" data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                          المزيد <i class="mdi mdi-chevron-down"></i>
                                        </button>
                                        <div class="dropdown-menu" style="min-width: 120px;">
                                            <router-link :to="{ name: 'supplier_show', params: { supplier_id: supplier.id } }" tag="a" class="dropdown-item">
                                                <i class="far fa-eye"></i> رؤية
                                            </router-link>
                                            <a @click="$refs.FormModal.edit(supplier)" class="dropdown-item dropdownItem" href="javascript:;">
                                              <i class="far fa-edit"></i> تعديل
                                            </a>
                                            <a @click="$refs.FormModal.destroy(supplier.id)" class="dropdown-item dropdownItem" href="javascript:;">
                                              <i class="far fa-trash-alt"></i> حذف
                                            </a>
                                        </div>
                                    </div>
                                  </td>
                              </tr>
                              </tbody>
                          </table>
                          <pagination-nav v-if="suppliers.last_page>1" :pageSize="5" :currentPage="2" :GoToPage="GoToPage" />
                      </div>
                      <div v-else class="card-body">
                          <beat-loader v-if="loader" :loading="loader" color="#5578eb"></beat-loader>
                          <p v-else class="card-title-desc" style="text-align: center;">
                            لا يوجد بيانات حتى الآن
                          </p>
                      </div>
                  </div>
              </div> <!-- end col -->
          </div> <!-- end row -->
      </div> <!-- container-fluid -->
    </div>
  </layout-default>
</template>  


<script>
import LayoutDefault from '../../layouts/App';
import PaginationNav from '../../components/pagination';
import FormModal from '../../components/modals/supplier/add';
import axios from 'axios';

export default {
  components: {LayoutDefault,PaginationNav,FormModal},
  data() {
      return {suppliers:null,loader:false}
    },  
  created() {
    this.fetchData();
  },
  methods: {
    fetchData(num=1) {
      this.suppliers=null;
      this.loader=true;
      let data = {params: {page:num}};
      axios.get('/api/suppliers',data).then(({data}) => {
          // console.log(data.id=="032E02B4-0499-05F3-FE06-0F0700080009")
          
          this.suppliers=data.success.suppliers;
          if(!data.success.suppliers.data.length&&num!=1){
              this.fetchData(1)
          }           
          this.loader=false;
      });  
    },
  },
}
</script>